<template>
  <div class="vip-list">
    <div :class="'vip-list__image--' + textPosition" v-if="image">
      <img :src="image" alt="">
    </div>
    <div :class="'vip-list__wave--' + textPosition">
      <img :src="require('@/assets/img/main/upwave.png')" alt="">
    </div>
    <div :class="'vip-list__wrapper--' + textPosition">
      <div class="container">
        <h4 :class="'vip-list__header--' + textPosition">
          {{ header }}
        </h4>
        <div :class="'vip-list__text--' + textPosition">
          {{ text }}
        </div>
        <ul :class="'vip-list__content--' + textPosition">
          <vip-list-item v-for="item in list"
                           :title="item.title"
                           :text="item.text"
                           :icon="item.icon"
                           :textPosition="textPosition"
                           :key="item.title"
          
          ></vip-list-item>
        </ul>
        <div :class="'vip-list__subheader--' + textPosition">
          {{ subheader }}
        </div>
        <div :class="'vip-list__btn--' + textPosition" v-if="this.btnName">
          <register-btn :link="link"
                        :name="btnName"
          ></register-btn>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    import RegisterBtn from "@/components/RegisterBtn";
    import VipListItem from "@/components/pageblocks/VipListItem";

    export default {
        name: "VipListBlock",
        components: {VipListItem, RegisterBtn},
        props: ['header', 'subheader', 'text', 'image', 'btn-name', 'link', 'text-position', 'list'],
    }
</script>

<style lang="sass" scoped>
  .vip-list
    width: 100%
    height: 100%
    position: relative
    
    &__wrapper
      position: relative
      overflow: hidden
      
      &--left
        position: absolute
        width: 100%
        height: 100%
        left: 0
        top: 0
        padding: 20% 0
        clip-path: polygon(0 8%, 23% 0, 49% 5%, 91% 0, 100% 4%, 100% 100%, 0 100%)
        overflow: hidden
        &:before
          content: ''
          display: block
          width: 100%
          height: 100%
          position: absolute
          top: 0
          left: 0
          background-image: linear-gradient(145deg, rgb(47, 96, 210) 0%, rgba(47, 96, 210, 0.7) 50%, rgba(0, 93, 232, 0) 100%)
        &:after
          content: ''
          display: block
          width: 100%
          height: 100%
          position: absolute
          top: 0
          left: 0
          background-image: linear-gradient(0deg, rgb(47, 96, 210) 0%, rgba(47, 96, 210, 0.7) 50%, rgba(0, 93, 232, 0) 100%), url("../../assets/img/about/glass.png")
        
        .container
          display: grid
          grid-template-columns: repeat(12, 1fr)
          grid-template-rows: repeat(4, 1fr)
          grid-column-gap: 0px
          grid-row-gap: 0px
          position: relative
          z-index: 1
      &--right
        background-image: url("../../assets/img/vip/viplistbg.png")
        background-size: 100% 100%
        padding: rem(100) 0
        margin: rem(50) 0
        .container
          display: flex
          flex-direction: column
          align-items: flex-end
          padding: 0 rem(110)
    &__image--left
      width: 100%
      height: auto
      background-image: linear-gradient(0deg, rgb(36, 80, 193) 0%, rgb(0, 93, 232) 100%)
      clip-path: polygon(0 8%, 23% 0, 49% 5%, 91% 0, 100% 4%, 100% 100%, 0 100%)
      img
        width: 100%
    &__image--right
      @extend .container
      position: absolute
      left: 0
      right: 0
      margin: 0 auto
      top: rem(90)
      text-align: left
      img
        width: 40%
        max-width: 350px
        margin-left: rem(110)
    
    &__header
      margin-bottom: 10px
      font-family: $mainFont
      font-size: rem(46)
      font-weight: 700
      line-height: rem(55)
      
      &--center
        @extend .vip-list__header
        width: 100%
        text-align: center
        color: rgb(0, 5, 33)
      
      &--left
        @extend .vip-list__header
        text-align: left
        display: inline-block
        grid-area: 1 / 1 / 2 / 5
        color: $whiteText
        
      &--right
        @extend .vip-list__header
        width: 50%
        float: right
        text-align: left
        color: $whiteText
        padding-bottom: rem(10)
    
    &__text
      font-family: $mainFont
      font-size: rem(16)
      font-weight: 400
      line-height: rem(22)
      
      &--center
        @extend .vip-list__text
        display: block
        width: 50%
        min-width: rem(500)
        height: auto
        color: rgb(99, 110, 126)
        margin: 0 auto
      
      &--left
        @extend .vip-list__text
        display: inline-block
        color: $whiteText
        grid-area: 2 / 1 / 3 / 5
        text-align: left
        
      &--right
        @extend .vip-list__text
        display: inline-block
        width: 50%
        float: right
        color: $whiteText
        text-align: left
        margin-top: 10px
        padding-right: rem(80)
        order: 1
    
    &__content
      list-style: none
      
      &--center
        @extend .vip-list__content
        display: grid
        grid-column-gap: 20px
        grid-row-gap: 20px
        grid-template-columns: repeat(3, 1fr)
        grid-template-rows: repeat(2, 1fr)
        margin-top: rem(55)
        margin-bottom: rem(50)
      
      &--left
        @extend .vip-list__content
        grid-area: 1 / 6 / 3 / 13
        
      &--right
        @extend .vip-list__content
        width: 120%
        position: relative
        left: 10%
        text-align: center
        display: flex
        flex-direction: row
        flex-wrap: wrap
        justify-content: space-between
        align-items: flex-end
        align-content: flex-end
        order: 4
        
        
    
    &__subheader
      font-family: $mainFont
      font-size: rem(24)
      font-weight: 500
      line-height: rem(33)
      
      &--center
        @extend .vip-list__subheader
        display: block
        text-align: center
        margin: 0 auto
        padding-bottom: rem(100)
        width: 50%
        height: auto
        color: rgb(0, 5, 33)
      &--right
        @extend .vip-list__header
        display: block
        text-align: center
        margin: 0 auto
        padding-bottom: rem(50)
        padding-top: rem(120)
        width: 100%
        height: auto
        color: $whiteText
        order: 3
        
        
    
    &__btn
      display: block

      
      &--left
        @extend .vip-list__btn
        grid-area: 3 / 1 / 4 / 5
      &--right
        @extend .vip-list__btn
        order: 2
        width: 50%
        text-align: left
        margin-top: rem(50)
    
    &__wave
      display: block
      width: 100%
      height: 20%
      position: absolute
      z-index: 1
      
      img
        width: 100%
      
      &--left
        @extend .vip-list__wave
        top: -10%
        left: 0
        transform:  rotateX(180deg)
      
      &--right
        @extend .vip-list__wave
        top: -5%
        left: 0
        display: none
      
      &--center
        @extend .vip-list__wave
        display: none


</style>